import React from 'react';
import { View, Text, StyleSheet, ScrollView } from 'react-native';
import { useTheme } from '../theme/ThemeContext';

interface GameEndProps {
    teams: Array<string[]>;
    gamePoints: Array<number>;
    playerId: string;
    players: Array<{
        id: string;
        name: string;
        isAgent: boolean;
    }>;
    gamePointsReversed: boolean;
}

export const GameEnd: React.FC<GameEndProps> = ({
    teams,
    gamePoints,
    playerId,
    players,
    gamePointsReversed
}) => {
    const { colors } = useTheme();
    
    const styles = StyleSheet.create({
        container: {
            flex: 1,
            padding: 20,
        },
        title: {
            fontSize: 24,
            fontWeight: 'bold',
            textAlign: 'center',
            marginBottom: 10,
        },
        subtitle: {
            fontSize: 18,
            textAlign: 'center',
            marginBottom: 20,
        },
        scoreList: {
            flex: 1,
        },
        teamRow: {
            flexDirection: 'row',
            justifyContent: 'space-between',
            alignItems: 'center',
            padding: 15,
            backgroundColor: colors.teamScoreCardBackground,
            borderRadius: 8,
            marginBottom: 10,
            borderWidth: 1,
            borderColor: colors.teamScoreCardBorder,
        },
        playerTeamRow: {
            backgroundColor: colors.playerTeamBackground,
            borderColor: colors.playerTeamBorder,
        },
        winnerRow: {
            borderWidth: 2,
            borderColor: 'gold',
        },
        rankAndTeam: {
            flexDirection: 'row',
            flex: 1,
            alignItems: 'flex-start',
        },
        rank: {
            fontSize: 18,
            fontWeight: 'bold',
            marginRight: 10,
            width: 25,
        },
        teamMembers: {
            flex: 1,
        },
        teamTitle: {
            fontSize: 16,
            fontWeight: 'bold',
            marginBottom: 5,
        },
        playerName: {
            fontSize: 14,
            marginLeft: 10,
            color: colors.statusText,
        },
        currentPlayerName: {
            fontWeight: 'bold',
            color: colors.currentPlayerText,
        },
        playerTeamText: {
            color: colors.playerTeamScoreText,
        },
        points: {
            fontSize: 18,
            fontWeight: 'bold',
            color: colors.teamScoreText,
        },
    });

    // Find which team the player belongs to
    const playerTeamIndex = teams.findIndex(team => team.includes(playerId));
    
    // Combine teams and points for sorting
    const teamsWithPoints = teams.map((team, index) => ({
        teamIndex: index,
        teamMembers: team,
        points: gamePoints[index] || 0,
        isPlayerTeam: index === playerTeamIndex
    }));
    
    // Sort teams by points (higher points first, unless reversed)
    const sortedTeams = [...teamsWithPoints].sort((a, b) => {
        if (gamePointsReversed) {
            return a.points - b.points; // Lower points win if reversed
        }
        return b.points - a.points; // Higher points win normally
    });

    return (
        <View style={styles.container}>
            <Text style={[styles.title, { color: colors.titleText }]}>Game Finished</Text>
            <Text style={[styles.subtitle, { color: colors.subtitleText }]}>Final Scores</Text>
            
            <ScrollView style={styles.scoreList}>
                {sortedTeams.map((team, index) => {
                    const isWinner = index === 0;
                    return (
                        <View 
                            key={`team-${team.teamIndex}`} 
                            style={[
                                styles.teamRow, 
                                team.isPlayerTeam && styles.playerTeamRow,
                                isWinner && styles.winnerRow
                            ]}
                        >
                            <View style={styles.rankAndTeam}>
                                <Text style={[
                                    styles.rank, 
                                    team.isPlayerTeam && styles.playerTeamText
                                ]}>
                                    {index + 1}.
                                </Text>
                                <View style={styles.teamMembers}>
                                    <Text style={[
                                        styles.teamTitle, 
                                        team.isPlayerTeam && styles.playerTeamText
                                    ]}>
                                        Team {team.teamIndex + 1} {team.isPlayerTeam && '(Your Team)'}
                                        {isWinner && ' 🏆'}
                                    </Text>
                                    {team.teamMembers.map(memberId => {
                                        const player = players.find(p => p.id === memberId);
                                        return (
                                            <Text 
                                                key={memberId} 
                                                style={[
                                                    styles.playerName,
                                                    memberId === playerId && styles.currentPlayerName,
                                                    team.isPlayerTeam && styles.playerTeamText
                                                ]}
                                            >
                                                {player?.name || 'Unknown'} {memberId === playerId && '(You)'}
                                                {player?.isAgent && ' (AI)'}
                                            </Text>
                                        );
                                    })}
                                </View>
                            </View>
                            <Text style={[
                                styles.points, 
                                team.isPlayerTeam && styles.playerTeamText
                            ]}>
                                {team.points} pts
                            </Text>
                        </View>
                    );
                })}
            </ScrollView>
        </View>
    );
}; 