import React, { useEffect, useState, useCallback } from 'react';
import { View, Text, Button, ScrollView, StyleSheet, BackHandler, AppState } from 'react-native';
import { SocketService } from '../services/socket.service';
import { GameScreenState } from '../types/game-ui';
import { GameManagement } from '../components/GameManagement';
import { GamePlay } from '../components/GamePlay';
import { GameEnd } from '../components/GameEnd';
import { GameBroadcastState, GamePhase } from '../shared/types/game-state';
import { LeaveGameButton } from '../components/LeaveGameButton';
import { useTheme } from '../theme/ThemeContext';
import { useNavigation, useFocusEffect } from '@react-navigation/native';
import { BroadcastTypes } from '../shared/types/actions';
import { ConfirmDialog } from '../components/ConfirmDialog';
import { ShareGameLink } from '../components/ShareGameLink';
import { CustomAlert } from '../utils/alert';

interface GameScreenProps {
    route: any;
}

export const GameScreen: React.FC<GameScreenProps> = ({ route }) => {
    const { gameId, playerName } = route.params;
    const [gameState, setGameState] = useState<GameBroadcastState | null>(null);
    const [screenState, setScreenState] = useState<GameScreenState>({
        mode: 'management',
        isCurrentPlayer: false
    });
    const [showLeaveConfirm, setShowLeaveConfirm] = useState(false);
    const [appState, setAppState] = useState(AppState.currentState);
    const socketService = SocketService.getInstance();
    const { colors } = useTheme();
    const navigation = useNavigation();

    console.log('GameScreen mounted with gameId:', gameId, 'and playerName:', playerName);

    // Handle hardware back button (Android)
    useFocusEffect(
        useCallback(() => {
            const onBackPress = () => {
                // Show leave game confirmation
                setShowLeaveConfirm(true);
                return true; // Prevent default back behavior
            };

            BackHandler.addEventListener('hardwareBackPress', onBackPress);
            
            // Set up navigation options for the header back button
            navigation.setOptions({
                headerLeft: () => null, // Remove the default back button
                // If you want a custom back button, you can add it here
            });

            return () => BackHandler.removeEventListener('hardwareBackPress', onBackPress);
        }, [navigation])
    );

    // Handle app state changes (foreground/background)
    useEffect(() => {
        const subscription = AppState.addEventListener('change', nextAppState => {
            console.log('App state changed from', appState, 'to', nextAppState);
            
            // App has come back to foreground
            if (appState.match(/inactive|background/) && nextAppState === 'active') {
                console.log('App has come back to the foreground!');
                
                // Check socket connection status
                if (!socketService.isConnected()) {
                    console.log('Socket disconnected while app was in background, attempting to reconnect...');
                    
                    // Try to reconnect
                    socketService.reconnect().catch(error => {
                        console.error('Failed to reconnect socket:', error);
                        
                        // Navigation to login screen if reconnection fails
                        CustomAlert.alert(
                            "Connection Lost",
                            "Your connection to the game was lost. Please log in again.",
                            [
                                { 
                                    text: "OK", 
                                    onPress: () => navigation.reset({
                                        index: 0,
                                        routes: [{ name: 'Login' as never }],
                                    })
                                }
                            ]
                        );
                    });
                }
            }
            
            setAppState(nextAppState);
        });
        
        return () => {
            subscription.remove();
        };
    }, [appState, navigation]);

    useEffect(() => {
        console.log('useEffect called');
        
        const handleNotInGame = (data: { gameId: string, playerId: string }) => {
            console.log('handleNotInGame called with data:', data);
            console.log('currentGameId:', gameId);
            console.log('currentUserId:', socketService.getCurrentUser()?.userId);
            
            if (data.gameId === gameId && data.playerId === socketService.getCurrentUser()?.userId) {
                console.log('Navigating to Home screen due to NOT_IN_GAME event');
                navigation.reset({
                    index: 0,
                    routes: [{ name: 'Home' as never }],
                });
            }
        };
        
        socketService.on(BroadcastTypes.NOT_IN_GAME, handleNotInGame);
        
        socketService.onGameState((state: GameBroadcastState) => {
            console.log('Game state received:', state);
            setGameState(state);
            
            setScreenState(prev => ({
                ...prev,
                mode: state.public.phase === 'PLAYING' ? 'playing' : 'management',
                isCurrentPlayer: state.public.players[state.public.currentPlayerIndex]?.name === playerName
            }));
        });

        return () => {
            socketService.off(BroadcastTypes.NOT_IN_GAME, handleNotInGame);
        };
    }, [gameId, navigation]);

    const handleAddAgent = () => {
        socketService.addAgent(gameId);
    };

    const handleStartGame = () => {
        socketService.startGame(gameId);
    };

    const handlePlayCard = (cardIndex: number) => {
        socketService.playCard(gameId, cardIndex);
    };

    const handleExtraAction = (actionType: string, actionData: any) => {
        socketService.extraAction(gameId, actionType, actionData);
    };

    const handleKickPlayer = (playerId: string) => {
        socketService.kickPlayer(gameId, playerId);
    };

    const handleReorderPlayer = (playerId: string, direction: 'up' | 'down') => {
        socketService.reorderPlayer(gameId, playerId, direction);
    };

    if (!gameState) {
        return <Text>Loading...</Text>;
    }

    const renderGameContent = () => {
        if (gameState.public.phase === GamePhase.FINISHED) {
            return (
                <GameEnd 
                    teams={gameState.public.gameSpecificData?.teams || []}
                    gamePoints={gameState.public.gameSpecificData?.gamePoints || []}
                    playerId={socketService.getCurrentUser()?.userId || ''}
                    players={gameState.public.players}
                    gamePointsReversed={gameState.public.gameCharacteristics.gamePointsReversed}
                />
            );
        } else if (screenState.mode === 'management') {
            return (
                <GameManagement
                    gameId={gameId}
                    onAddAgent={handleAddAgent}
                    onStartGame={handleStartGame}
                    playerCount={gameState.public.players.length}
                    players={gameState.public.players.map(p => ({
                        ...p,
                        isOwner: p.id === gameState.public.owner
                    }))}
                    isOwner={gameState.public.owner === socketService.getCurrentUser()?.userId}
                    onKickPlayer={handleKickPlayer}
                    onReorderPlayer={handleReorderPlayer}
                    gameCharacteristics={gameState.public.gameCharacteristics}
                />
            );
        } else {
            return (
                <GamePlay
                    gameState={gameState}
                    playerName={playerName}
                    playerId={socketService.getCurrentUser()?.userId || ''}
                    onPlayCard={handlePlayCard}
                    onExtraAction={handleExtraAction}
                />
            );
        }
    };

    const styles = StyleSheet.create({
        container: {
            flex: 1,
            padding: 20,
            position: 'relative',
        },
        headerButtons: {
            flexDirection: 'row',
            justifyContent: 'space-between',
            width: '100%',
            padding: 10,
            marginBottom: 10,
        },
        title: {
            fontSize: 24,
            fontWeight: 'bold',
            marginTop: 10,
            textAlign: 'center'
        },
        subtitle: {
            fontSize: 18,
            fontWeight: 'bold',
            marginBottom: 10,
        },
        controls: {
            flexDirection: 'row',
            justifyContent: 'space-around',
            marginBottom: 20,
        },
        status: {
            fontSize: 16,
            marginBottom: 20,
        },
        trickContainer: {
            marginBottom: 20,
        },
        trick: {
            flexGrow: 0,
            height: 100,
        },
        handContainer: {
            marginTop: 'auto',
        },
        hand: {
            flexGrow: 0,
            height: 100,
        },
        scoreboard: {
            marginTop: 20,
        },
    });

    return (
        <View style={[styles.container, { backgroundColor: colors.containerBackground }]}>
            <View style={styles.headerButtons}>
                <ShareGameLink 
                    gameId={gameId} 
                    isOwner={gameState?.public?.owner === socketService.getCurrentUser()?.userId} 
                />
                <LeaveGameButton gameId={gameId} />
            </View>
            
            <Text style={[styles.title, { color: colors.titleText }]}>{gameId}</Text>
            
            {renderGameContent()}
            <ConfirmDialog
                visible={showLeaveConfirm}
                title="Leave Game"
                message="Are you sure you want to leave this game? This action cannot be undone."
                onConfirm={async () => {
                    try {
                        const response = await socketService.leaveGame(gameId);
                        if (response.success) {
                            navigation.reset({
                                index: 0,
                                routes: [{ name: 'Home' as never }],
                            });
                        }
                    } catch (error) {
                        console.error('Error leaving game:', error);
                    }
                    setShowLeaveConfirm(false);
                }}
                onCancel={() => setShowLeaveConfirm(false)}
            />
        </View>
    );
};
