import React from 'react';
import { View, Text, Modal, TouchableOpacity, StyleSheet } from 'react-native';
import { useTheme } from '../theme/ThemeContext';
interface ConfirmDialogProps {
    visible: boolean;
    title: string;
    message: string;
    onConfirm: () => void;
    onCancel: () => void;
}

export const ConfirmDialog: React.FC<ConfirmDialogProps> = ({
    visible,
    title,
    message,
    onConfirm,
    onCancel
}) => {
    const { colors } = useTheme();

    const styles = StyleSheet.create({
        overlay: {
            flex: 1,
            backgroundColor: colors.dialogOverlay,
            justifyContent: 'center',
            alignItems: 'center',
        },
        dialog: {
            backgroundColor: colors.dialogBackground,
            borderRadius: 10,
            padding: 20,
            width: '80%',
            maxWidth: 400,
        },
        title: {
            fontSize: 18,
            fontWeight: 'bold',
            marginBottom: 10,
            textAlign: 'center',
        },
        message: {
            fontSize: 16,
            marginBottom: 20,
            textAlign: 'center',
        },
        buttonContainer: {
            flexDirection: 'row',
            justifyContent: 'space-between',
        },
        button: {
            flex: 1,
            padding: 10,
            borderRadius: 5,
            marginHorizontal: 5,
        },
        cancelButton: {
            backgroundColor: colors.dialogCancelButton,
        },
        confirmButton: {
            backgroundColor: colors.dialogConfirmButton,
        },
        buttonText: {
            color: colors.dialogButtonText,
            textAlign: 'center',
            fontSize: 16,
        },
    });

    return (
        <Modal
            transparent
            visible={visible}
            animationType="fade"
            onRequestClose={onCancel}
        >
            <View style={styles.overlay}>
                <View style={styles.dialog}>
                    <Text style={styles.title}>{title}</Text>
                    <Text style={styles.message}>{message}</Text>
                    <View style={styles.buttonContainer}>
                        <TouchableOpacity 
                            style={[styles.button, styles.cancelButton]} 
                            onPress={onCancel}
                        >
                            <Text style={styles.buttonText}>Cancel</Text>
                        </TouchableOpacity>
                        <TouchableOpacity 
                            style={[styles.button, styles.confirmButton]} 
                            onPress={onConfirm}
                        >
                            <Text style={styles.buttonText}>Leave Game</Text>
                        </TouchableOpacity>
                    </View>
                </View>
            </View>
        </Modal>
    );
};
