import React, { useState, useEffect } from 'react';
import { View, TextInput, StyleSheet, Text, TouchableOpacity } from 'react-native';
import { SocketService } from '../services/socket.service';
import { useTheme } from '../theme/ThemeContext';
import { CustomAlert } from '../utils/alert';

export const LoginScreen = ({ navigation, route }: { navigation: any, route: any }) => {
    const [username, setUsername] = useState('');
    const socketService = SocketService.getInstance();
    const { colors } = useTheme();
    
    // Extract gameId from route params if it exists
    const gameId = route.params?.gameId;

    const styles = StyleSheet.create({
        container: {
            flex: 1,
            padding: 20,
            justifyContent: 'center',
            backgroundColor: colors.containerBackground,
        },
        title: {
            fontSize: 24,
            textAlign: 'center',
            marginBottom: 30,
            color: colors.titleText,
        },
        input: {
            borderWidth: 1,
            borderColor: colors.inputBorder,
            backgroundColor: colors.inputBackground,
            padding: 10,
            marginBottom: 20,
            borderRadius: 5,
            color: colors.titleText,
        },
        buttonContainer: {
            marginTop: 10,
        },
        button: {
            backgroundColor: colors.primaryButton,
            padding: 12,
            borderRadius: 5,
            alignItems: 'center',
        },
        buttonText: {
            color: colors.primaryButtonText,
            fontSize: 16,
            fontWeight: 'bold',
        },
        subtitle: {
            fontSize: 16,
            marginBottom: 10,
        }
    });

    useEffect(() => {
        if (gameId) {
            console.log('LoginScreen received gameId:', gameId);
        }
    }, [gameId]);

    const handleLogin = async () => {
        if (!username.trim()) {
            CustomAlert.alert('Error', 'Please enter a username');
            return;
        }

        try {
            console.log('Logging in with username:', username);
            const response = await socketService.login(username);
            
            if (response.success) {
                // Store the username globally in the SocketService
                socketService.setCurrentUser(username, response.userId!);
                console.log('Login successful.');
                
                // If we have a gameId from deep link, join game directly
                if (gameId) {
                    console.log('Joining game directly with ID:', gameId);
                    try {
                        await socketService.connect(username);
                        const joinResponse = await socketService.joinGame(gameId, username) as { success: boolean; error?: string };
                        
                        if (joinResponse.success) {
                            navigation.replace('Game', { gameId, playerName: username });
                        } else {
                            // Show specific error message based on the error
                            const errorMessage = joinResponse.error || 'Could not join the game. You will be redirected to the home screen.';
                            
                            CustomAlert.alert(
                                'Game Join Failed', 
                                errorMessage,
                                [{ text: 'OK', onPress: () => navigation.replace('Home') }]
                            );
                        }
                    } catch (error) {
                        console.error('Error joining game:', error);
                        CustomAlert.alert(
                            'Connection Error', 
                            'Failed to connect to the game server. Please try again later.',
                            [{ text: 'OK', onPress: () => navigation.replace('Home') }]
                        );
                    }
                } else {
                    // Normal login flow
                    navigation.replace('Home');
                }
            } else {
                console.log('Login failed:', response.error || 'Username already taken');
                // Show more helpful error message
                const errorMessage = response.error || 'This username is already in use. Please choose a different one.';
                CustomAlert.alert('Login Failed', errorMessage);
            }
        } catch (error) {
            console.error('Server connection error:', error);
            CustomAlert.alert(
                'Connection Error', 
                'Could not connect to the server. Please check your internet connection and try again.'
            );
        }
    };

    return (
        <View style={styles.container}>
            <Text style={styles.title}>
                {gameId ? 'Enter Your Username to Join Game' : 'Enter Your Username'}
            </Text>
            {gameId && (
                <Text style={[styles.subtitle, { color: colors.subtitleText }]}>
                    You're joining game: {gameId}
                </Text>
            )}
            <TextInput
                style={styles.input}
                onChangeText={setUsername}
                value={username}
                placeholder="Username"
                placeholderTextColor={colors.playerStatsText}
                autoCapitalize="none"
                autoCorrect={false}
            />
            <View style={styles.buttonContainer}>
                <TouchableOpacity 
                    style={styles.button}
                    onPress={handleLogin}
                >
                    <Text style={styles.buttonText}>
                        {gameId ? "Login & Join Game" : "Login"}
                    </Text>
                </TouchableOpacity>
            </View>
        </View>
    );
}; 