import { io, Socket } from 'socket.io-client';
import { AvailableGames } from '../shared/types/game-state';
import { AuthActions, GamePlayActions, GameAdminActions, MainActions } from '../shared/types/actions';
import { BroadcastTypes } from '../shared/types/actions';
import { GameBroadcastState } from '../shared/types/game-state';
import config from '../config/env';

interface LoginResponse {
    success: boolean;
    userId?: string;
    error?: string;
}

interface GameResponse {
    success: boolean;
    gameId?: string;
    error?: string;
}

export class SocketService {
    private socket: Socket;
    private static instance: SocketService;
    private currentUser: { username: string; userId: string } | null = null;
    private reconnectAttempts: number = 0;
    private readonly MAX_RECONNECT_ATTEMPTS = 5;
    private heartbeatInterval: NodeJS.Timeout | null = null;
    private readonly HEARTBEAT_INTERVAL = 60 * 1000; // Send heartbeat every minute
    private eventHandlers: Map<string, Function[]> = new Map();

    private constructor() {
        this.socket = io(config.socketUrl, {
            transports: ['websocket', 'polling'],
            path: config.socketPath,
            autoConnect: false,
            reconnection: true,
            reconnectionAttempts: this.MAX_RECONNECT_ATTEMPTS,
            reconnectionDelay: 1000,
            timeout: 10000
        });

        console.log('SocketService constructor called, connecting to:', config.socketUrl);
        
        this.setupSocketListeners();
    }

    private setupSocketListeners() {
        this.socket.on('connect', () => {
            console.log('Socket CONNECTED, ID:', this.socket.id);
            this.reconnectAttempts = 0;
        });

        this.socket.on('disconnect', (reason) => {
            console.log('Socket DISCONNECTED, was ID:', this.socket.id, 'Reason:', reason);
            if (reason === 'io server disconnect') {
                // the disconnection was initiated by the server, reconnect manually
                this.socket.connect();
            }
        });

        this.socket.on('connect_error', (error) => {
            console.error('Connection error:', error);
            this.reconnectAttempts++;
            
            if (this.reconnectAttempts >= this.MAX_RECONNECT_ATTEMPTS) {
                console.error('Max reconnection attempts reached');
                this.socket.disconnect();
            }
        });

        this.socket.on('reconnect', (attemptNumber) => {
            console.log('Socket RECONNECTED, new ID:', this.socket.id, 'Attempt:', attemptNumber);
        });

        this.socket.on('reconnect_attempt', (attemptNumber) => {
            console.log('Socket RECONNECTING, attempt:', attemptNumber);
        });

        this.socket.on(BroadcastTypes.STATE, (state: GameBroadcastState) => {
            console.log('Received state update for socket ID:', this.socket.id);
        });

        this.socket.on(BroadcastTypes.GAME_DELETED, (data: { gameId: string }) => {
            console.log('Received game deleted event for socket ID:', this.socket.id, 'Game ID:', data.gameId);
        });

        this.socket.on(BroadcastTypes.NOT_IN_GAME, (data: { gameId: string, playerId: string }) => {
            console.log('Received not in game event for socket ID:', this.socket.id, 'Game ID:', data.gameId, 'Player ID:', data.playerId);
            
            this.triggerEvent(BroadcastTypes.NOT_IN_GAME, data);
        });

        this.socket.on('forceDisconnect', (data: { reason: string }) => {
            console.log('[Client Force Disconnect]', {
                socketId: this.socket.id,
                userId: this.currentUser?.userId,
                reason: data.reason,
                timestamp: new Date().toISOString()
            });
            this.clearHeartbeat();
            this.clearCurrentUser();
            this.socket.disconnect();
        });
    }

    public static getInstance(): SocketService {
        if (!SocketService.instance) {
            SocketService.instance = new SocketService();
        }
        return SocketService.instance;
    }

    public connect(playerName: string): Promise<void> {
        console.log('Connect called, current socket ID:', this.socket.id);
        return new Promise((resolve, reject) => {
            if (this.socket.connected) {
                console.log('Socket already connected with ID:', this.socket.id);
                resolve();
                return;
            }
            
            this.socket.connect();
            this.socket.on('connect', () => {
                console.log('Connected to server with ID:', this.socket.id);
                resolve();
            });
            
            this.socket.on('connect_error', (error) => {
                reject(error);
            });
        });
    }

    public createGame(playerName: string, gameType: AvailableGames, gameName: string): Promise<GameResponse> {
        return new Promise<GameResponse>((resolve, reject) => {
            console.log('Emitting createGame event with playerName:', playerName,
                'and gameType:', gameType);

            const timeout = setTimeout(() => {
                reject(new Error('createGame timeout'));
            }, 5000);

            this.socket.emit(MainActions.CREATE_GAME, { playerName, gameType, gameName}, (response: GameResponse) => {
                console.log('Received response from createGame:', response);
                clearTimeout(timeout);
                resolve(response);
            });

            this.socket.on('error', (error) => {
                console.error('Socket error:', error);
                reject(error);
            });
        });
    }

    public joinGame(gameId: string, playerName: string): Promise<{ success: boolean; error?: string }> {
        return new Promise<{ success: boolean; error?: string }>((resolve, reject) => {
            console.log('Emitting joinGame event with gameId:', gameId, 'playerName:', playerName);

            // Add timeout to detect if callback never happens
            const timeout = setTimeout(() => {
                reject(new Error('joinGame timeout'));
            }, 5000);

            this.socket.emit(MainActions.JOIN_GAME, { gameId, playerName }, (response: { success: boolean; error?: string }) => {
                console.log('Received response from joinGame:', response);
                clearTimeout(timeout);
                resolve(response);
            });

            // Add error handler
            this.socket.on('error', (error) => {
                console.error('Socket error:', error);
                reject(error);
            });
        });
    }

    public addAgent(gameId: string): Promise<{ success: boolean }> {
        return new Promise((resolve) => {
            this.socket.emit(GameAdminActions.ADD_AGENT, { gameId }, (response: { success: boolean }) => {
                resolve(response);
            });
        });
    }

    public startGame(gameId: string): Promise<{ success: boolean }> {
        return new Promise((resolve) => {
            this.socket.emit(GameAdminActions.START_GAME, { gameId }, (response: { success: boolean }) => {
                resolve(response);
            });
        });
    }

    public playCard(gameId: string, cardIndex: number): Promise<{ success: boolean }> {
        return new Promise((resolve) => {
            this.socket.emit(GamePlayActions.PLAY_CARD, { gameId, cardIndex }, (response: { success: boolean }) => {
                resolve(response);
            });
        });
    }

    public onGameState(callback: (state: GameBroadcastState) => void) {

        console.log('Setting up game state listener for socket:', {
            socketId: this.socket.id,
            userId: this.currentUser?.userId,
            connected: this.socket.connected
        });
    
        this.socket.onAny((eventName, ...args) => {
            console.log('Received event:', eventName, 'Socket ID:', this.socket.id, 'Args:', args);
        });
    
        this.socket.on(BroadcastTypes.STATE, (state: GameBroadcastState) => {
            console.log('Received game state for socket:', {
                socketId: this.socket.id,
                userId: this.currentUser?.userId,
                state
            });
            callback(state);
        });    

        this.socket.on('connect', () => {
            console.log('Socket connected:', this.socket.id);
        });

        this.socket.on('disconnect', () => {
            console.log('Socket disconnected');
        });
    }

    public disconnect() {
        this.socket.disconnect();
    }

    public setupHeartbeat() {
        if (this.heartbeatInterval) {
            clearInterval(this.heartbeatInterval);
            console.log('[Client Heartbeat] Cleared existing heartbeat interval');
        }

        console.log('[Client Heartbeat] Setting up new heartbeat interval');
        this.heartbeatInterval = setInterval(() => {
            if (this.socket.connected && this.currentUser) {
                console.log('[Client Heartbeat] Sending heartbeat');

                // Set a timeout to detect if the heartbeat response never comes back
                const heartbeatTimeout = setTimeout(() => {
                    console.warn('[Client Heartbeat] Heartbeat response timeout - connection may be stale');
                    // Force reconnection attempt
                    this.socket.disconnect();
                    this.reconnect().catch(error => {
                        console.error('Failed to reconnect after heartbeat timeout:', error);
                    });
                }, 10000); // 10 second timeout

                this.socket.emit(AuthActions.HEARTBEAT, {}, (response: { success: boolean }) => {
                    clearTimeout(heartbeatTimeout);
                    console.log('[Client Heartbeat] Received response:', {
                        socketId: this.socket.id,
                        userId: this.currentUser?.userId,
                        success: response.success,
                        timestamp: new Date().toISOString()
                    });

                    if (!response.success) {
                        console.warn('[Client Heartbeat] Failed, initiating logout');
                        this.logout();
                    }
                });
            } else {
                console.log('[Client Heartbeat] Skipped: Socket disconnected or no user', {
                    connected: this.socket.connected,
                    hasUser: !!this.currentUser
                });
            }
        }, this.HEARTBEAT_INTERVAL);
    }

    private clearHeartbeat() {
        if (this.heartbeatInterval) {
            console.log('[Client Heartbeat] Clearing heartbeat interval');
            clearInterval(this.heartbeatInterval);
            this.heartbeatInterval = null;
        }
    }

    public login(username: string): Promise<LoginResponse> {
        return new Promise((resolve) => {
            this.socket.connect();
            console.log('Logging in with socket ID:', this.socket.id);
            
            this.socket.emit(AuthActions.LOGIN, { username }, (response: LoginResponse) => {
                console.log('Login response received for socket:', {
                    socketId: this.socket.id,
                    response
                });
                if (response.success) {
                    this.setCurrentUser(username, response.userId!);
                    this.setupHeartbeat(); // Start heartbeat after successful login
                } else {
                    this.socket.disconnect();
                }
                resolve(response);
            });
        });
    }

    public extraAction(gameId: string, actionType: string, actionData: any): Promise<GameResponse> {
        return new Promise((resolve) => {
            this.socket.emit(GamePlayActions.EXTRA_ACTION, { 
                gameId, 
                actionType, 
                actionData 
            }, (response: GameResponse) => {
                resolve(response);
            });
        });
    }

    public setCurrentUser(username: string, userId: string) {
        this.currentUser = { username, userId };
    }

    public getCurrentUser() {
        return this.currentUser;
    }

    public clearCurrentUser() {
        this.currentUser = null;
    }

    public async logout(): Promise<void> {
        this.clearHeartbeat(); // Stop heartbeat before logout
        if (this.currentUser) {
            await new Promise<void>((resolve) => {
                this.socket.emit(AuthActions.LOGOUT, {}, () => {
                    this.clearCurrentUser();
                    this.socket.disconnect();
                    resolve();
                });
            });
        }
    }

    public leaveGame(gameId: string): Promise<{ success: boolean; error?: string }> {
        return new Promise((resolve, reject) => {
            console.log('Emitting leaveGame event with gameId:', gameId);

            const timeout = setTimeout(() => {
                reject(new Error('leaveGame timeout'));
            }, 5000);

            this.socket.emit(MainActions.LEAVE_GAME, { gameId }, (response: { success: boolean; error?: string }) => {
                console.log('Received response from leaveGame:', response);
                clearTimeout(timeout);
                resolve(response);
            });

            this.socket.on('error', (error) => {
                console.error('Socket error:', error);
                reject(error);
            });
        });
    }

    public kickPlayer(gameId: string, playerId: string): void {
        this.socket.emit(GameAdminActions.KICK_PLAYER, { gameId, playerId });
    }

    public on(event: string, callback: Function) {
        if (!this.eventHandlers.has(event)) {
            this.eventHandlers.set(event, []);
        }
        this.eventHandlers.get(event)?.push(callback);
    }
    
    public off(event: string, callback: Function) {
        if (!this.eventHandlers.has(event)) return;
        
        const handlers = this.eventHandlers.get(event) || [];
        const index = handlers.indexOf(callback);
        if (index !== -1) {
            handlers.splice(index, 1);
        }
    }
    
    private triggerEvent(event: string, ...args: any[]) {
        const handlers = this.eventHandlers.get(event) || [];
        handlers.forEach(handler => handler(...args));
    }

    public isConnected(): boolean {
        return this.socket.connected;
    }
    
    public reconnect(): Promise<void> {
        return new Promise((resolve, reject) => {
            if (this.socket.connected) {
                console.log('Socket already connected, no need to reconnect');
                resolve();
                return;
            }
            
            console.log('Attempting to reconnect socket...');
            
            // Set timeout for reconnection attempt
            const timeout = setTimeout(() => {
                reject(new Error('Socket reconnection timeout'));
            }, 5000);
            
            const onConnect = () => {
                clearTimeout(timeout);
                this.socket.off('connect', onConnect);
                this.socket.off('connect_error', onError);
                
                // If we have a current user, reestablish authentication
                if (this.currentUser) {
                    this.login(this.currentUser.username)
                        .then(response => {
                            if (response.success) {
                                console.log('Re-authenticated after reconnect');
                                resolve();
                            } else {
                                reject(new Error('Failed to re-authenticate after reconnection'));
                            }
                        })
                        .catch(reject);
                } else {
                    resolve();
                }
            };
            
            const onError = (error: any) => {
                clearTimeout(timeout);
                this.socket.off('connect', onConnect);
                this.socket.off('connect_error', onError);
                reject(error);
            };
            
            this.socket.on('connect', onConnect);
            this.socket.on('connect_error', onError);
            
            // Connect the socket
            this.socket.connect();
        });
    }

    public getSocket(): Socket {
        return this.socket;
    }

    public reorderPlayer(gameId: string, playerId: string, direction: 'up' | 'down') {
        this.socket.emit(GameAdminActions.REORDER_PLAYER, {
            gameId,
            playerId,
            direction
        });
    }

    public isLoggedIn(): boolean {
        return !!this.currentUser;
    }
}
