import React, { useEffect, useState } from 'react';
import { View, Text, Button, StyleSheet, TextInput } from 'react-native';
import { Picker } from '@react-native-picker/picker';
import { SocketService } from '../services/socket.service';
import { AvailableGames } from '../shared/types/game-state';
import { useTheme } from '../theme/ThemeContext';

const GAME_TYPES: AvailableGames[] = ['SUECA'];

// Function to generate a random 8 character hexadecimal string
const generateRandomGameName = (): string => {
    return Math.random().toString(16).substring(2, 10);
};

export const GameCreationScreen: React.FC<{ route: any, navigation: any }> =
    ({route, navigation}) => {
        const { colors } = useTheme();
        console.log('GameCreationScreen rendered');
        console.log('Route params:', route.params);
        const { playerName } = route.params;
        const socketService = SocketService.getInstance();
        const [gameType, setGameType] = useState<AvailableGames>(GAME_TYPES[0]);
        const [gameName, setGameName] = useState<string>('');
    
        const handleCreateGame = async () => {
            console.log('Creating game');
            await socketService.connect(playerName);
            console.log('Connected to socket');
            // Use the provided game name or generate a random one if empty
            const finalGameName = gameName.trim() || generateRandomGameName();
            const response = await socketService.createGame(playerName, gameType, finalGameName);
            if (response.success && response.gameId) {
                console.log('Game created successfully');
                navigation.navigate('Game', { gameId: response.gameId, playerName });
            }
        };

        const styles = StyleSheet.create({
            container: {
                flex: 1,
                padding: 20,
            },
            title: {
                fontSize: 24,
                marginBottom: 20,
            },
            formGroup: {
                marginBottom: 20,
            },
            label: {
                marginBottom: 10,
            },
            picker: {
                padding: 10,
            },
            input: {
                borderWidth: 1,
                borderColor: colors.inputBorder,
                borderRadius: 5,
                padding: 10,
                color: colors.inputText,
                backgroundColor: colors.inputBackground,
            }
        });

        return (
            <View style={[styles.container, { backgroundColor: colors.containerBackground }]}>
                <Text style={[styles.title, { color: colors.titleText }]}>Game Creation</Text>
                <View style={styles.formGroup}>
                    <Text style={[styles.label, { color: colors.subtitleText }]}>
                        Select Game Type:
                    </Text>
                    <Picker
                        selectedValue={gameType}
                        onValueChange={(itemValue: AvailableGames) => setGameType(itemValue)}
                        style={[styles.picker, { backgroundColor: colors.pickerBackground }]}
                    >
                        {GAME_TYPES.map((game) => (
                            <Picker.Item 
                                key={game} 
                                label={game.replace('_', ' ')} 
                                value={game}
                            />
                        ))}
                    </Picker>
                </View>
                <View style={styles.formGroup}>
                    <Text style={[styles.label, { color: colors.subtitleText }]}>
                        Game Name (optional):
                    </Text>
                    <TextInput
                        style={styles.input}
                        value={gameName}
                        onChangeText={setGameName}
                        placeholder="Enter game name or leave blank for random"
                        placeholderTextColor={colors.placeholderText}
                    />
                </View>
                <Button 
                    title="Create Game" 
                    onPress={handleCreateGame} 
                    color={colors.primaryButton}
                />
            </View>
        );
    }