import React, { useState } from 'react';
import { View, TextInput, Button, StyleSheet } from 'react-native';
import { SocketService } from '../services/socket.service';
import { useTheme } from '../theme/ThemeContext';
import { CustomAlert } from '../utils/alert';


export const JoinGameScreen: React.FC<{ route: any, navigation: any }> =
    ({route, navigation}) => {
        const { colors } = useTheme();
        const { playerName } = route.params;
        const socketService = SocketService.getInstance();
        const [gameId, setGameId] = useState('');

        const handleJoinGame = async () => {
            if (!gameId.trim()) {
                CustomAlert.alert('Error', 'Please enter a game ID');
                return;
            }
            
            try {
                await socketService.connect(playerName);
                const result = await socketService.joinGame(gameId, playerName);
                
                if (result.success) {
                    navigation.navigate('Game', { gameId, playerName });
                } else {
                    // Enhanced error messaging based on specific error types
                    if (result.error === 'Game not found') {
                        CustomAlert.alert('Error', 'Game not found. Please check the game ID and try again.');
                    } else if (result.error === 'Cannot join a game in progress') {
                        CustomAlert.alert('Error', 'Cannot join a game that is already in progress.');
                    } else if (result.error === 'Game is full') {
                        CustomAlert.alert('Error', 'This game is full and cannot accept more players.');
                    } else {
                        CustomAlert.alert('Error', result.error || 'Failed to join the game.');
                    }
                }
            } catch (error) {
                console.error('Error joining game:', error);
                CustomAlert.alert('Connection Error', 'Failed to connect to the game server. Please try again later.');
            }
        };

        const styles = StyleSheet.create({
            container: {
                flex: 1,
                padding: 20,
                justifyContent: 'center',
                backgroundColor: colors.containerBackground
            },
            input: {
                borderWidth: 1,
                borderColor: colors.inputBorder,
                padding: 10,
                marginBottom: 20,
                borderRadius: 5,
                backgroundColor: colors.inputBackground,
                color: colors.inputText
            },
            buttonContainer: {
                gap: 10,
            },
        });

        return (
            <View style={styles.container}>
                <TextInput
                    style={styles.input}
                    placeholder="Enter game name"
                    onChangeText={(text) => setGameId(text)}
                    placeholderTextColor={colors.placeholderText}
                    value={gameId}
                />
                <Button 
                    title="Join Game" 
                    onPress={handleJoinGame} 
                    color={colors.primaryButton}
                />
            </View>
        );
    }
    