import React from 'react';
import { TouchableOpacity, Text, StyleSheet, Platform } from 'react-native';
import { Card } from '../shared/types/game-state';
import { useTheme } from '../theme/ThemeContext';

if (Platform.OS === 'web') {
    require('./Card.web.css');
}

interface CardProps {
    card: Card;
    onPress?: () => void;
    disabled?: boolean;
}

export const CardRender: React.FC<CardProps> = ({ card, onPress, disabled }) => {
    const suitSymbols = {
        hearts: '♥',
        diamonds: '♦',
        clubs: '♣',
        spades: '♠'
    };

    const { colors } = useTheme();

    const styles = StyleSheet.create({
        card: {
            width: 60,
            height: 90,
            backgroundColor: colors.cardBackground,
            borderRadius: 8,
            borderWidth: 1,
            borderColor: colors.cardBorder,
            padding: 5,
            margin: 2,
            alignItems: 'center',
            justifyContent: 'center',
        },
        disabled: {
            opacity: 0.5,
        },
        value: {
            fontSize: 24,
            color: colors.cardTextBlack,
        },
        suit: {
            fontSize: 32,
            color: colors.cardTextBlack,
        },
        redText: {
            color: colors.cardTextRed,
        },
    });

    const valueMap: { [key: number]: string } = {
        11: 'J',
        12: 'Q',
        13: 'K',
        14: 'A'
    };

    const isRed = card.suit === 'hearts' || card.suit === 'diamonds';

    return (
        <TouchableOpacity
            style={[styles.card, disabled && styles.disabled]}
            // @ts-ignore
            className={Platform.OS === 'web' ? 'card-hover' : undefined}
            onPress={onPress}
            disabled={disabled}
            // @ts-ignore
            {...(Platform.OS === 'web' && {
                style: [
                    styles.card,
                    disabled && styles.disabled,
                    { cursor: 'pointer' }
                ]
            })}
        >
            <Text style={[styles.value, isRed && styles.redText]}>
                {valueMap[card.value] || card.value}
            </Text>
            <Text style={[styles.suit, isRed && styles.redText]}>
                {suitSymbols[card.suit]}
            </Text>
        </TouchableOpacity>
    );
};
