import { Platform, Alert as RNAlert } from 'react-native';

interface AlertOptions {
  title: string;
  message: string;
  buttons?: Array<{
    text: string;
    onPress?: () => void;
    style?: 'default' | 'cancel' | 'destructive';
  }>;
  cancelable?: boolean;
}

export const CustomAlert = {
  alert(title: string, message: string, buttons?: Array<{ 
    text: string; 
    onPress?: () => void; 
    style?: 'default' | 'cancel' | 'destructive' 
  }>, options?: { cancelable?: boolean }) {
    if (Platform.OS === 'web') {
      // For web, use the browser's alert() and handle button clicks
      if (!buttons || buttons.length === 0) {
        // Simple alert with OK button
        window.alert(`${title}\n\n${message}`);
      } else if (buttons.length === 1) {
        // Alert with one button
        window.alert(`${title}\n\n${message}`);
        if (buttons[0].onPress) buttons[0].onPress();
      } else if (buttons.length === 2) {
        // Use confirm for two buttons (usually OK/Cancel)
        const confirmResult = window.confirm(`${title}\n\n${message}`);
        
        // Find the non-cancel button (usually the OK/Yes button)
        const confirmButton = buttons.find(button => button.style !== 'cancel');
        const cancelButton = buttons.find(button => button.style === 'cancel');
        
        if (confirmResult && confirmButton?.onPress) {
          confirmButton.onPress();
        } else if (!confirmResult && cancelButton?.onPress) {
          cancelButton.onPress();
        }
      } else {
        // For more than 2 buttons, show a basic alert and execute the first non-cancel action
        window.alert(`${title}\n\n${message}`);
        const defaultButton = buttons.find(button => button.style !== 'cancel');
        if (defaultButton?.onPress) defaultButton.onPress();
      }
    } else {
      // For mobile, use React Native's Alert
      RNAlert.alert(title, message, buttons, options);
    }
  }
};