import React from 'react';
import { View, Text, Button, StyleSheet, ScrollView, TouchableOpacity } from 'react-native';
import { useTheme } from '../theme/ThemeContext';

export interface GameManagementProps {
    gameId: string;
    onAddAgent: () => void;
    onStartGame: () => void;
    playerCount: number;
    players: Array<{
        id: string;
        name: string;
        isAgent: boolean;
        isOwner?: boolean;
    }>;
    isOwner: boolean;
    onKickPlayer: (playerId: string) => void;
    onReorderPlayer: (playerId: string, direction: 'up' | 'down') => void;
    gameCharacteristics: {
        minPlayers: number;
        maxPlayers: number;
        playerGroupSize: number;
        extraActions: string[];
        gamePointsReversed: boolean;
    };
}

export const GameManagement: React.FC<GameManagementProps> = ({
    gameId,
    onAddAgent,
    onStartGame,
    playerCount,
    players,
    isOwner,
    onKickPlayer,
    onReorderPlayer,
    gameCharacteristics
}) => {
    const { colors } = useTheme();
    
    const styles = StyleSheet.create({
        container: {
            flex: 1,
            padding: 20,
        },
        subtitle: {
            fontSize: 18,
            fontWeight: 'bold',
            marginBottom: 10,
        },
        controls: {
            flexDirection: 'column',
            justifyContent: 'space-around',
            marginTop: 20,
        },
        playerListContainer: {
            marginTop: 10,
            marginBottom: 10,
            height: 160,
            borderWidth: 1,
            borderColor: '#ddd',
            borderRadius: 5,
        },
        playerList: {
            flex: 1,
        },
        playerRow: {
            flexDirection: 'row',
            justifyContent: 'space-between',
            paddingVertical: 8,
            paddingHorizontal: 10,
            borderBottomWidth: 1,
            borderBottomColor: '#eee',
            alignItems: 'center',
        },
        playerName: {
            fontSize: 16,
            flex: 1,
        },
        playerActions: {
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'flex-end',
        },
        actionButton: {
            paddingHorizontal: 2,
        },
        kickButton: {
            color: 'red',
            fontSize: 18,
            fontWeight: 'bold',
        },
        moveButton: {
            fontSize: 18,
            fontWeight: 'bold',
            width: 18,
            textAlign: 'center',
        },
        upButton: {
            color: 'green',
        },
        downButton: {
            color: 'blue',
        },
        buttonSpacer: {
            marginTop: 10,
        }
    });

    const hasCorrectNumberOfPlayers = playerCount >= (gameCharacteristics?.minPlayers || 2) && 
                                      playerCount <= (gameCharacteristics?.maxPlayers || Infinity) && 
                                      (playerCount % (gameCharacteristics?.playerGroupSize || 1) === 0);
    
    return (
        <View style={styles.container}>
            <Text style={[styles.subtitle, { color: colors.titleText }]}>Game Setup</Text>
            <Text style={{ color: colors.statusText }}>Players: {playerCount} (Min: {gameCharacteristics?.minPlayers || 2})</Text>
            
            <View style={styles.playerListContainer}>
                <ScrollView style={styles.playerList}>
                    {players.map((player, index) => (
                        <View key={player.id} style={styles.playerRow}>
                            <Text style={[styles.playerName, { color: colors.statusText }]}>
                                {player.name} {player.isAgent ? '(AI)' : ''} {player.isOwner ? '(Owner)' : ''}
                            </Text>
                            {isOwner && !player.isOwner && (
                                <View style={styles.playerActions}>
                                    <TouchableOpacity
                                        onPress={() => onReorderPlayer(player.id, 'up')}
                                        style={styles.actionButton}
                                    >
                                        <Text style={[styles.moveButton, styles.upButton]}>↑</Text>
                                    </TouchableOpacity>
                                    <TouchableOpacity
                                        onPress={() => onReorderPlayer(player.id, 'down')}
                                        style={styles.actionButton}
                                    >
                                        <Text style={[styles.moveButton, styles.downButton]}>↓</Text>
                                    </TouchableOpacity>
                                    <TouchableOpacity 
                                        onPress={() => onKickPlayer(player.id)}
                                        style={styles.actionButton}
                                    >
                                        <Text style={styles.kickButton}>✕</Text>
                                    </TouchableOpacity>
                                </View>
                            )}
                        </View>
                    ))}
                </ScrollView>
            </View>
            
            <View style={styles.controls}>
                {isOwner && (
                    <>
                        <Button title="Add AI Player" onPress={onAddAgent} />
                        {hasCorrectNumberOfPlayers && (
                            <View style={styles.buttonSpacer}>
                                <Button title="Start Game" onPress={onStartGame} />
                            </View>
                        )}
                    </>
                )}
            </View>
        </View>
    );
};