import React from 'react';
import { View, Button, Text, ScrollView, StyleSheet, ViewStyle, Dimensions } from 'react-native';
import { CardRender } from './Card';
import { GameBroadcastState } from '../shared/types/game-state';
import { Card } from '../shared/types/game-state';
import { useTheme } from '../theme/ThemeContext';

interface GamePlayProps {
    gameState: GameBroadcastState;
    playerName: string;
    playerId: string;
    onPlayCard: (index: number) => void;
    onExtraAction: (actionType: string, actionData: any) => void;
}

// Game design properties to control layout
interface GameDesignProps {
    TABLE_SIZE: number;                  // Size of the table in px
    TABLE_MEMBERS_EXPANSION_FACTOR: number; // Expansion factor for spacing elements
    CARD_RADIUS_FACTOR: number;          // Position factor for cards from center
    PLAYER_RADIUS_FACTOR: number;        // Position factor for players from center  
    MIN_GAME_AREA_HEIGHT: number;        // Minimum height for game area
    CARD_SIZE: {width: number, height: number}; // Size of cards
    PLAYER_INFO_MIN_WIDTH: number;       // Minimum width for player info boxes
}

// Default design properties
const defaultDesignProps: GameDesignProps = {
    TABLE_SIZE: 280,
    TABLE_MEMBERS_EXPANSION_FACTOR: 2.5, // 200% expansion as requested
    CARD_RADIUS_FACTOR: 0.5,           // Cards at 25% of table radius
    PLAYER_RADIUS_FACTOR: 0.5,          // Players at 70% of table radius * expansion factor
    MIN_GAME_AREA_HEIGHT: 350,
    CARD_SIZE: {width: 60, height: 85},
    PLAYER_INFO_MIN_WIDTH: 80
};

export const GamePlay: React.FC<GamePlayProps> = ({
    gameState,
    playerName,
    playerId,
    onPlayCard,
    onExtraAction
}) => {
    const { colors } = useTheme();
    
    const styles = StyleSheet.create({
        container: {
            flex: 1,
            position: 'relative',
            padding: 10,
            width: '100%',
            alignSelf: 'center',
            display: 'flex',
            flexDirection: 'column',
        },
        headerRow: {
            flexDirection: 'row',
            justifyContent: 'space-between',
            alignItems: 'center',
            marginBottom: 10,
        },
        status: {
            fontSize: 16,
            flex: 1,
            color: colors.statusText,
        },
        subtitle: {
            fontSize: 18,
            fontWeight: 'bold',
            marginBottom: 5,
            color: colors.subtitleText,
        },
        trickContainer: {
            position: 'absolute',
            width: '100%',
            height: '100%',
            alignItems: 'center',
            justifyContent: 'center',
            zIndex: 1,
        },
        trickTable: {
            position: 'relative',
            alignItems: 'center',
            justifyContent: 'center',
        },
        tableBackground: {
            position: 'absolute',
            width: '100%',
            height: '100%',
            borderRadius: 1000,
            backgroundColor: colors.tableFelt,
            borderWidth: 4,
            borderColor: colors.tableEdge,
        },
        cardPositioner: {
            position: 'absolute',
            width: 0,
            height: 0,
            alignItems: 'center',
            justifyContent: 'center',
        },
        trickCardContainer: {
            position: 'absolute',
            alignItems: 'center',
        },
        handContainer: {
            width: '100%',
            marginTop: 5,
            marginBottom: 10,
            flexShrink: 0,
        },
        handWrapper: {
            width: '100%',
            height: 100,  // Fixed height
        },
        handContent: {
            alignItems: 'center',
            paddingHorizontal: 5,
        },
        hand: {
            flexGrow: 0,
        },
        extraActionContainer: {
            marginTop: 10,
            marginBottom: 10,
        },
        gameArea: {
            flex: 1,
            position: 'relative',
            marginVertical: 10,
        },
        playAreaContainer: {
            width: '100%',
            height: '100%',
            position: 'relative',
            alignItems: 'center',
            justifyContent: 'center',
        },
        playerTable: {
            position: 'absolute',
            width: 0,  // Set to 0 to ensure proper centering
            height: 0,  // Set to 0 to ensure proper centering
            alignItems: 'center',
            justifyContent: 'center',
            zIndex: 2,
        },
        playerInfo: {
            position: 'absolute',
            alignItems: 'center',
            backgroundColor: colors.playerInfoBackground,
            padding: 8,
            borderRadius: 8,
            minWidth: 80,
            borderWidth: 1,
            borderColor: colors.playerInfoBorder,
        },
        winningPlayerInfo: {
            backgroundColor: colors.winningPlayerBackground || '#4a9eff30', // fallback color if theme doesn't define it
            borderColor: colors.winningPlayerBorder || '#4a9eff',
        },
        playerNameFixed: {
            fontSize: 12, // Smaller font
            fontWeight: 'bold',
            marginBottom: 2,
        },
        currentPlayerName: {
            color: colors.currentPlayerText,
            fontWeight: 'bold',
        },
        playerStats: {
            fontSize: 10, // Smaller font
            color: colors.playerStatsText,
        },
        trumpInfo: {
            position: 'absolute',
            top: 5,
            right: 5,
            alignItems: 'center',
            backgroundColor: colors.trumpInfoBackground,
            padding: 5, // Reduced padding
            borderRadius: 6,
            borderWidth: 1,
            borderColor: colors.trumpInfoBorder,
            zIndex: 10,
        },
        trumpTitle: {
            fontSize: 14,
            fontWeight: 'bold',
            marginBottom: 4,
        },
        trumpHolder: {
            fontSize: 12,
            color: colors.playerStatsText,
            marginTop: 4,
        },
        teamScoreContainer: {
            flexDirection: 'row',
            justifyContent: 'flex-end',
            gap: 5, // Reduced gap
        },
        teamScoreCard: {
            backgroundColor: colors.teamScoreCardBackground,
            borderRadius: 4,
            paddingVertical: 3,
            paddingHorizontal: 6,
            minWidth: 50,
            alignItems: 'center',
            borderWidth: 1,
            borderColor: colors.teamScoreCardBorder,
        },
        playerTeamCard: {
            backgroundColor: colors.playerTeamBackground,
            borderColor: colors.playerTeamBorder,
        },
        teamName: {
            fontSize: 12,
            fontWeight: '500',
            textAlign: 'center',
        },
        teamScore: {
            fontSize: 14,
            fontWeight: 'bold',
            color: colors.teamScoreText,
        },
        playerTeamScore: {
            color: colors.playerTeamScoreText,
        },
    });

    const currentPlayerIndex = gameState.public.currentPlayerIndex;
    const currentPlayerId = gameState.public.players[currentPlayerIndex].id;
    const isCurrentPlayer = currentPlayerId === playerId;
    const isResolvingTrick = gameState.public.gameSpecificData?.isResolvingTrick;
    const isResolvingTurnEnd = gameState.public.isResolvingTurnEnd;
    const isResolvingGameEnd = gameState.public.isResolvingGameEnd;
    const hand = gameState.private?.hand || [];
    const currentTrick = gameState.public.gameSpecificData?.currentTrick || [];
    const scores = gameState.public.gameSpecificData?.scores || [];
    const canToggle = gameState.public.gameSpecificData?.canToggle;
    const trumpCard = gameState.public.gameSpecificData?.trumpCard;
    const trumpHolder = gameState.public.gameSpecificData?.trumpHolder;
    const teams = gameState.public.gameSpecificData?.teams || [];
    const gamePoints = gameState.public.gameSpecificData?.gamePoints || [];
    
    // Find which team the player belongs to
    const playerTeamIndex = teams.findIndex((team: string[]) => team.includes(playerId));

    // Near the top of your component, after the props destructuring
    const windowWidth = Dimensions.get('window').width;
    const windowHeight = Dimensions.get('window').height;
    const responsiveStyles = getResponsiveStyles(windowWidth, windowHeight);
    
    // Use the design props, can be adjusted dynamically if needed
    const designProps = defaultDesignProps;

    // Render cards in hand
    const renderHand = () => (
        <View style={styles.handWrapper}>
            <ScrollView horizontal contentContainerStyle={styles.handContent}>
                {hand.map((card: any, index: number) => (
                    <CardRender
                        key={index}
                        card={card}
                        onPress={() => isCurrentPlayer && onPlayCard(index)}
                    />
                ))}
            </ScrollView>
        </View>
    );

    // Render current trick
    const renderCurrentTrick = () => {
        const myPlayerIndex = gameState.public.players.findIndex(p => p.id === playerId);
        const numPlayers = gameState.public.players.length;
        
        // Calculate card radius based on design props
        const tableRadius = designProps.TABLE_SIZE / 2;
        const CARD_RADIUS = tableRadius * designProps.CARD_RADIUS_FACTOR;

        const getCardPosition = (playerIndex: number): ViewStyle => {
            const relativePos = (playerIndex - myPlayerIndex + numPlayers) % numPlayers;
            const angle = -Math.PI/2 - (relativePos * 2 * Math.PI / numPlayers);
            // The minus is a hack
            const x = - CARD_RADIUS * Math.cos(angle);
            const y = - CARD_RADIUS * Math.sin(angle);

            return {
                position: 'absolute',
                transform: [
                    { translateX: x },
                    { translateY: y },
                    { rotate: `${angle + Math.PI/2}rad` }
                ] as any[]
            };
        };

        return (
            <View style={styles.trickContainer}>
                <View style={[styles.trickTable, {
                    width: designProps.TABLE_SIZE, 
                    height: designProps.TABLE_SIZE
                }]}>
                    <View style={styles.tableBackground} />                    
                    <View style={styles.cardPositioner}>
                        {/* Render cards if they exist */}
                        {currentTrick?.map(({playerId: cardPlayerId, card}: {playerId: string, card: Card}) => {
                            const playerIndex = gameState.public.players.findIndex(p => p.id === cardPlayerId);
                            return (
                                <View 
                                    key={cardPlayerId} 
                                    style={[
                                        styles.trickCardContainer, 
                                        getCardPosition(playerIndex),
                                        {
                                            width: designProps.CARD_SIZE.width, 
                                            height: designProps.CARD_SIZE.height
                                        }
                                    ]}
                                >
                                    <CardRender card={card} />
                                </View>
                            );
                        })}
                    </View>
                </View>
            </View>
        );
    };

    // Render players
    const renderPlayers = () => {
        const myPlayerIndex = gameState.public.players.findIndex(p => p.id === playerId);
        const numPlayers = gameState.public.players.length;
        
        // Calculate player radius based on design props
        const tableRadius = designProps.TABLE_SIZE / 2;
        const PLAYER_RADIUS = tableRadius * designProps.PLAYER_RADIUS_FACTOR * 
                             designProps.TABLE_MEMBERS_EXPANSION_FACTOR;

        // Add this function to determine winning teams
        const getWinningTeams = () => {
            if (!isResolvingTurnEnd || !teams || !scores) return [];
            
            // Calculate team scores for this turn
            const teamScores = teams.map((team: string[]) => {
                return team.reduce((sum: number, playerId: string) => {
                    const playerScore = scores.find((s: {playerId: string, score: number}) => s.playerId === playerId)?.score || 0;
                    return sum + playerScore;
                }, 0);
            });

            // Find highest score
            const maxScore = Math.max(...teamScores);
            
            // Return indices of teams with the highest score
            return teamScores
                .map((score: number, index: number) => score === maxScore ? index : -1)
                .filter((index: number) => index !== -1);
        };

        const winningTeams = getWinningTeams();

        const getPlayerPosition = (playerIndex: number): ViewStyle => {
            const relativePos = (playerIndex - myPlayerIndex + numPlayers) % numPlayers;
            const angle = -Math.PI/2 - (relativePos * 2 * Math.PI / numPlayers);
            const x = - PLAYER_RADIUS * Math.cos(angle);
            const y = - PLAYER_RADIUS * Math.sin(angle);

            return {
                position: 'absolute',
                transform: [
                    { translateX: x },
                    { translateY: y }
                ] as any[]
            };
        };

        return (
            <View style={styles.playerTable}>
                {gameState.public.players.map((player, index) => {
                    const playerScore = gameState.public.gameSpecificData?.scores?.find(
                        (score: {playerId: string, score: number}) => score.playerId === player.id
                    )?.score || 0;

                    // Find which team this player belongs to
                    const playerTeamIndex = teams.findIndex((team: string[]) => 
                        team.includes(player.id)
                    );

                    // Check if player is on a winning team
                    const isWinningPlayer = winningTeams.includes(playerTeamIndex);

                    return (
                        <View 
                            key={player.id}
                            style={[
                                styles.playerInfo, 
                                getPlayerPosition(index),
                                { minWidth: designProps.PLAYER_INFO_MIN_WIDTH },
                                isWinningPlayer && isResolvingTurnEnd && styles.winningPlayerInfo
                            ]}
                        >
                            <Text style={[
                                styles.playerNameFixed,
                                player.id === playerId && styles.currentPlayerName
                            ]}>
                                {player.name}
                            </Text>
                            {(isResolvingTurnEnd || gameState.public.isResolvingGameEnd) && (
                                <Text style={styles.playerStats}>
                                    Points: {playerScore}
                                </Text>
                            )}
                        </View>
                    );
                })}
            </View>
        );
    };

    // Render extra action button if available
    const renderExtraAction = () => {
        if (!isCurrentPlayer || !canToggle || !onExtraAction) return null;
        return (
            <View style={styles.extraActionContainer}>
                <Button
                    title={`Toggle ${gameState.public.gameSpecificData?.isHighWins ? 'High' : 'Low'} Wins`}
                    onPress={() => onExtraAction('TOGGLE_WIN_CONDITION', {})}
                />
            </View>
        );
    };

    // Add new function to render trump info
    const renderTrumpInfo = () => {
        if (!trumpCard || !trumpHolder) return null;
        
        const trumpHolderName = gameState.public.players.find(p => p.id === trumpHolder)?.name;
        
        return (
            <View style={styles.trumpInfo}>
                <Text style={styles.trumpTitle}>Trump Card:</Text>
                <CardRender card={trumpCard} />
                <Text style={styles.trumpHolder}>Held by: {trumpHolderName}</Text>
            </View>
        );
    };

    // Render team scores more discreetly
    const renderTeamScores = () => {
        if (!teams || teams.length === 0 || !gamePoints) return null;
        
        return (
            <View style={styles.teamScoreContainer}>
                {teams.map((team: string[], index: number) => {
                    const teamScore = gamePoints[index] || 0;
                    const isPlayerTeam = index === playerTeamIndex;
                    
                    return (
                        <View 
                            key={`team-${index}`} 
                            style={[
                                styles.teamScoreCard, 
                                isPlayerTeam && styles.playerTeamCard
                            ]}
                        >
                            <Text style={styles.teamName}>
                                Team {index + 1}
                                {isPlayerTeam ? ' ★' : ''}
                            </Text>
                            <Text style={[
                                styles.teamScore,
                                isPlayerTeam && styles.playerTeamScore
                            ]}>
                                {teamScore}
                            </Text>
                        </View>
                    );
                })}
            </View>
        );
    };

    return (
        <View style={styles.container}>
            <View style={styles.headerRow}>
                <Text style={styles.status}>
                    {(() => {
                        if (isResolvingGameEnd) {
                            return "Game ending...";
                        } else if (isResolvingTurnEnd) {
                            return "Round ending...";
                        } else if (isResolvingTrick) {
                            return "Resolving trick...";
                        } else if (isCurrentPlayer) {
                            return "Your turn!";
                        } else {
                            return `Waiting for ${gameState.public.players[currentPlayerIndex].name}`;
                        }
                    })()}
                </Text>
                {renderTeamScores()}
            </View>
            <View style={[
                styles.gameArea, 
                responsiveStyles.gameArea,
                { minHeight: designProps.MIN_GAME_AREA_HEIGHT }
            ]}>
                <View style={styles.playAreaContainer}>
                    {renderTrumpInfo()}
                    {renderCurrentTrick()}
                    {renderPlayers()}
                </View>
            </View>
            <View style={styles.handContainer}>
                <Text style={styles.subtitle}>Your Hand:</Text>
                {renderHand()}
                {renderExtraAction()}
            </View>
        </View>
    );
};

// getResponsiveStyles stays outside
const getResponsiveStyles = (width: number, height: number) => ({
    gameArea: {
        maxHeight: height * 0.6,
    },
    playerTable: width <= 768 ? {
        maxWidth: 350,
        maxHeight: 350,
    } : {
        maxWidth: 450,
        maxHeight: 450,
    }
}); 