// Define the shape of our theme context
interface ThemeContextType {
  themeType: ThemeType;
  colors: ThemeColors;
  setThemeType: (type: ThemeType) => void;
  toggleTheme: () => void;
}

// Create the context with default values
const ThemeContext = createContext<ThemeContextType>({
  themeType: 'light',
  colors: lightColors,
  setThemeType: () => {},
  toggleTheme: () => {},
});

// Theme provider component
export const ThemeProvider: React.FC<{children: ReactNode}> = ({ children }) => {
  const [themeType, setThemeType] = useState<ThemeType>('dark');
  
  // Get the colors based on the current theme type
  const colors = themeType === 'light' ? lightColors : darkColors;
  
  // Function to toggle between light and dark themes
  const toggleTheme = () => {
    setThemeType(prevTheme => prevTheme === 'light' ? 'dark' : 'light');
  };

  return (
    <ThemeContext.Provider value={{ themeType, colors, setThemeType, toggleTheme }}>
      {children}
    </ThemeContext.Provider>
  );
};

// Custom hook to use the theme
export const useTheme = () => {
  const context = useContext(ThemeContext);
  if (context === undefined) {
    throw new Error('useTheme must be used within a ThemeProvider');
  }
  return context;
};

