import React from 'react';
import { TouchableOpacity, Text, StyleSheet, Share, Platform } from 'react-native';
import { useTheme } from '../theme/ThemeContext';
import { CustomAlert } from '../utils/alert';

interface ShareGameLinkProps {
    gameId: string;
    style?: any;
    isOwner: boolean;
}

export const ShareGameLink: React.FC<ShareGameLinkProps> = ({ gameId, style, isOwner }) => {
    const { colors } = useTheme();
    
    const styles = StyleSheet.create({
        shareButton: {
            backgroundColor: colors.shareButtonBackground,
            padding: 10,
            borderRadius: 5,
            // No absolute positioning here, will be positioned by parent
        },
        shareButtonText: {
            color: colors.shareButtonText,
            fontSize: 16,
            fontWeight: 'bold',
        },
    }); 

    // If not the owner, don't render the button
    if (!isOwner) return null;

    const handleShare = async () => {
        // Create deep link for the game
        const gameDeepLink = `cardgame://game/${gameId}`;
        
        // For web, create a URL - with special handling for GitHub Pages
        let webUrl;
        if (Platform.OS === 'web') {
            // Get the current pathname
            const pathname = window.location.pathname;
            
            // Extract the base path
            const basePath = pathname.endsWith('/') 
                ? pathname 
                : pathname.substring(0, pathname.lastIndexOf('/') + 1);
            
            // Check if we're on GitHub Pages by looking for github.io in the hostname
            const isGitHubPages = window.location.hostname.includes('github.io');
            
            if (isGitHubPages) {
                // For GitHub Pages, use query parameters
                webUrl = `${window.location.origin}${basePath}?gameId=${gameId}`;
            } else {
                // For all other web hosts, use the path-based approach
                webUrl = `${window.location.origin}${basePath}game/${gameId}`;
            }
            
            console.log('Generated share URL:', webUrl);
        }
        
        const url = Platform.OS === 'web' ? webUrl : gameDeepLink;
        
        try {
            if (Platform.OS === 'web') {
                // On web, use clipboard API
                navigator.clipboard.writeText(url || '')
                    .then(() => {
                        CustomAlert.alert('Success', 'Game link copied to clipboard! Share it with your friends.');
                    })
                    .catch(() => {
                        CustomAlert.alert('Error', 'Failed to copy the game link.');
                    });
            } else {
                // On native, use Share API
                const result = await Share.share({
                    message: `Join my card game! ${url}`,
                    url: url,
                    title: 'Join my card game'
                });
                
                if (result.action === Share.sharedAction) {
                    if (result.activityType) {
                        console.log(`Shared via ${result.activityType}`);
                    } else {
                        console.log('Shared successfully');
                    }
                } else if (result.action === Share.dismissedAction) {
                    console.log('Share dismissed');
                }
            }
        } catch (error) {
            console.error('Error sharing:', error);
            CustomAlert.alert('Error', 'Could not share the game link');
        }
    };

    return (
        <TouchableOpacity 
            style={[styles.shareButton, style]} 
            onPress={handleShare}
        >
            <Text style={styles.shareButtonText}>Share Game Link</Text>
        </TouchableOpacity>
    );
};
