import { AvailableGames } from './game-state';

// Authentication Actions
export enum AuthActions {
    LOGIN = 'auth/login',
    LOGOUT = 'auth/logout',
    HEARTBEAT = 'auth:heartbeat'
}

export type LoginPayload = {
    username: string;
}

export type LoginResponse = {
    success: boolean;
    error?: string;
    userId?: string;
}

// Main Service Actions
export enum MainActions {
    CREATE_GAME = 'main/createGame',
    JOIN_GAME = 'main/joinGame',
    LEAVE_GAME = 'main/leaveGame'
}

export type CreateGamePayload = {
    gameType: AvailableGames;
}

export type JoinGamePayload = {
    gameId: string;
}

// Game Administrative Actions
export enum GameAdminActions {
    ADD_AGENT = 'game/admin/addAgent',
    START_GAME = 'game/admin/startGame',
    KICK_PLAYER = 'game/admin/kickPlayer',
    LEAVE_GAME = 'game/admin/leaveGame',
    REORDER_PLAYER = 'game/admin/reorderPlayer'
}

export type KickPlayerPayload = {
    playerId: string;
}

// Game Play Actions
export enum GamePlayActions {
    PLAY_CARD = 'game/play/playCard',
    EXTRA_ACTION = 'game/play/extraAction'
}

export interface PlayCardPayload {
    cardIndex: number;
    gameId: string;
}

export interface ExtraActionPayload {
    actionType: string;
    actionData: Record<string, unknown>;
    gameId: string;
}

// Server Broadcast Types
export enum BroadcastTypes {
    STATE = 'broadcast/state',
    GAME_ERROR = 'broadcast/gameError',
    GAME_ENDED = 'broadcast/gameEnded',
    GAME_DELETED = 'broadcast/gameDeleted',
    NOT_IN_GAME = 'broadcast/notInGame'
}

export interface GameEndedPayload {
    gameId: string;
    winner?: string;
}

export interface GameErrorPayload {
    gameId: string;
    error: string;
}