import React from 'react';
import { View, Text, Button, StyleSheet } from 'react-native';
import { SocketService } from '../services/socket.service';
import { useTheme } from '../theme/ThemeContext';

export const HomeScreen: React.FC<{ navigation: any }> = ({ navigation }) => {
    const { colors, toggleTheme, themeType } = useTheme();

    const socketService = SocketService.getInstance();
    const username = socketService.getCurrentUser()?.username;

    const handleCreateGame = () => {
        console.log('Creating game');
        navigation.navigate('GameCreation', { playerName: username });
    };

    const handleJoinGame = () => {
        console.log('Joining game');
        navigation.navigate('JoinGame', { playerName: username });
    };

    const handleLogout = async () => {
        await socketService.logout();
        console.log('Logging out');
        navigation.replace('Login');
    };

    const styles = StyleSheet.create({
        container: {
            flex: 1,
            padding: 20,
            justifyContent: 'center',
        },
        welcome: {
            fontSize: 24,
            textAlign: 'center',
            marginBottom: 30,
            color: colors.welcomeText,
        },
        buttonContainer: {
            gap: 10,
        },
    });
    
    return (
        <View style={[styles.container, { backgroundColor: colors.containerBackground }]}>
            <Text style={styles.welcome}>Welcome, {username}!</Text>
            <View style={styles.buttonContainer}>
                <Button 
                    title="Create Game" 
                    onPress={handleCreateGame}
                    color={colors.primaryButton}
                />
                <Button 
                    title="Join Game" 
                    onPress={handleJoinGame}
                    color={colors.primaryButton}
                />
                <Button 
                    title="Logout" 
                    onPress={handleLogout} 
                    color={colors.logoutButton}
                />
            </View>
        </View>
    );
};
