export type AvailableGames = 'SUECA'

export interface Card {
    suit: 'hearts' | 'diamonds' | 'clubs' | 'spades';
    value: number;  // 2-14 (14 being Ace)
}

export interface PublicGameState {
    id: string;
    players: Array<{
        id: string;
        name: string;
        isAgent: boolean;
    }>;
    currentPlayerIndex: number;
    phase: GamePhase;
    owner?: string;
    gameSpecificData: any;
    isResolvingTrick: boolean;
    isResolvingTurnEnd: boolean;
    isResolvingGameEnd: boolean;
    gameCharacteristics: GameCharacteristics;
}

export interface PrivatePlayerState {
    hand: Card[];
    gameSpecificData?: any;
}

export interface GameCharacteristics {
    minPlayers: number;
    maxPlayers: number;
    playerGroupSize: number;
    extraActions: string[];
    gamePointsReversed: boolean;
}

export interface GameBroadcastState {
    public: PublicGameState;
    private?: PrivatePlayerState;
}

// Base interface for game-specific data
export interface BaseGameSpecificData {
    currentTrick?: Array<{playerId: string; card: Card}>;
    scores?: Array<{playerId: string; score: number}>;
    gamePoints?: Array<number>;
    teams?: Array<string[]>;
}

// Game-specific data interfaces
export interface HighLowGameData extends BaseGameSpecificData {
    isHighWins: boolean;
    canToggle: boolean;
}

export enum GamePhase {
    WAITING = 'WAITING',
    PLAYING = 'PLAYING',
    FINISHED = 'FINISHED'
}

export interface GameAction {
    type: 'PLAY_CARD' | 'JOIN_GAME' | 'START_GAME';
    playerId?: string;
    cardIndex?: number;
    gameId?: string;
}
