import React, { useEffect, useRef, useState } from 'react';
import { Platform, View, StatusBar, StyleSheet, Linking } from 'react-native';
import { NavigationContainer, DefaultTheme, NavigationContainerRef } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import { LoginScreen } from './src/screens/LoginScreen';
import { HomeScreen } from './src/screens/HomeScreen';
import { GameScreen } from './src/screens/GameScreen';
import { GameCreationScreen } from './src/screens/GameCreationScreen';
import { JoinGameScreen } from './src/screens/JoinGameScreen';
import { ThemeProvider, useTheme } from './src/theme/ThemeContext';
import { ThemeToggleButton } from './src/components/ThemeToggleButton';
import { SocketService } from './src/services/socket.service';
import { CustomAlert } from './src/utils/alert';

// Types for navigation
type RootStackParamList = {
  Login: { gameId?: string }; // Add gameId parameter to Login
  Home: undefined;
  Game: { gameId: string; playerName: string };
  GameCreation: { playerName: string };
  JoinGame: { playerName: string };
};

const WebStack = createStackNavigator<RootStackParamList>();
const NativeStack = createNativeStackNavigator<RootStackParamList>();

export default function App() {
  return (
    <ThemeProvider>
      <AppContent />
    </ThemeProvider>
  );
}

const AppContent = () => {
  const Stack = Platform.OS === 'web' ? WebStack : NativeStack;
  const { colors, themeType } = useTheme();
  const navigationRef = useRef<NavigationContainerRef<RootStackParamList>>(null);
  const [initialRoute, setInitialRoute] = useState<{name: keyof RootStackParamList; params: any}>({ 
    name: 'Login', 
    params: {} 
  });
  const [isReady, setIsReady] = useState(false);

  // Custom navigation theme based on our colors
  const navigationTheme = {
    ...DefaultTheme,
    colors: {
      ...DefaultTheme.colors,
      background: colors.appBackground,
      card: colors.containerBackground,
      text: colors.titleText,
      border: colors.inputBorder,
      primary: colors.primaryButton,
    },
  };

  // Handle deep links
  const handleDeepLink = async (url: string) => {
    if (!url) return;
    
    console.log('Received deep link:', url);
    
    // Parse the URL to get the game ID - handle both native deep links and web URLs
    let gameId;
    
    // Native deep link pattern
    const nativeMatch = url.match(/cardgame:\/\/game\/([^\/]+)/);
    if (nativeMatch) {
      gameId = nativeMatch[1];
    } 
    
    // Web URL pattern - updated to match even with subdirectories
    const webMatch = url.match(/\/game\/([^\/]+)(?:\/|$)/);
    if (webMatch) {
      gameId = webMatch[1];
    }
    
    if (gameId) {
      console.log('Deep link contains game ID:', gameId);
      
      const socketService = SocketService.getInstance();
      if (socketService.isLoggedIn()) {
        // If already logged in, navigate to the game directly
        const navigation = navigationRef.current;
        if (navigation) {
          // First connect to ensure socket is ready
          try {
            await socketService.connect(socketService.getCurrentUser()?.username || '');
            const joinResponse = await socketService.joinGame(gameId, socketService.getCurrentUser()?.username || '');
            
            if (joinResponse.success) {
              navigation.reset({
                index: 0,
                routes: [
                  { name: 'Game', params: { 
                    gameId, 
                    playerName: socketService.getCurrentUser()?.username 
                  }}
                ],
              });
            } else {
              // Show an alert with the specific error
              CustomAlert.alert(
                'Game Join Failed',
                joinResponse.error === 'Game not found or cannot join'
                  ? 'The game you tried to join does not exist or is no longer available.'
                  : 'Could not join the game.',
                [{ 
                  text: 'OK', 
                  onPress: () => navigation.reset({ index: 0, routes: [{ name: 'Home' }] }) 
                }]
              );
            }
          } catch (error) {
            console.error('Error joining game:', error);
            CustomAlert.alert(
              'Connection Error',
              'Failed to connect to the game server. Please try again later.',
              [{ 
                text: 'OK', 
                onPress: () => navigation.reset({ index: 0, routes: [{ name: 'Home' }] }) 
              }]
            );
          }
        }
      } else {
        // If not logged in, set initial route to Login with gameId
        setInitialRoute({ name: 'Login' as keyof RootStackParamList, params: { gameId } });
      }
    }
  };

  // Set up link listeners
  useEffect(() => {
    // For web, check multiple sources for game ID
    if (Platform.OS === 'web') {
      let gameId = null;
      
      // First check URL query parameters
      const urlParams = new URLSearchParams(window.location.search);
      gameId = urlParams.get('gameId');
      
      // If not found in URL params, check sessionStorage (for GitHub Pages)
      if (!gameId && typeof sessionStorage !== 'undefined') {
        gameId = sessionStorage.getItem('gameId');
        // Clear from sessionStorage after reading
        if (gameId) {
          console.log('Found game ID in sessionStorage:', gameId);
          sessionStorage.removeItem('gameId');
        }
      }
      
      // If still not found, check path
      if (!gameId) {
        const path = window.location.pathname;
        const webMatch = path.match(/\/game\/([^\/]+)(?:\/|$)/);
        if (webMatch) {
          gameId = webMatch[1];
        }
      }
      
      // Process the gameId if found
      if (gameId) {
        console.log('Found game ID:', gameId);
        handleDeepLink(gameId);
        
        // Clean URL if needed
        if (window.history && window.history.replaceState) {
          const cleanUrl = window.location.pathname;
          window.history.replaceState({}, document.title, cleanUrl);
        }
      }
    }

    // Regular deep link handling for all platforms
    const subscription = Linking.addEventListener('url', ({ url }) => {
      handleDeepLink(url);
    });

    // Handle the case where the app is opened from a link
    Linking.getInitialURL().then(url => {
      if (url) handleDeepLink(url);
      setIsReady(true);
    }).catch(err => {
      console.error('Error getting initial URL:', err);
      setIsReady(true);
    });

    return () => {
      subscription.remove();
    };
  }, []);

  useEffect(() => {
    const socketService = SocketService.getInstance();
    const socket = socketService.getSocket();
   
    // Set up global socket state listener
    const onDisconnect = (reason: any) => {
        console.log('Socket disconnected:', reason);
        if (reason === "io server disconnect" || reason === "transport close") {
            // Server forced disconnect or transport closed - try to reconnect
            setTimeout(() => {
                socketService.reconnect().catch(error => {
                    console.error('Failed to reconnect socket after disconnect:', error);
                });
            }, 1000);
        }
    };
    
    socket.on('disconnect', onDisconnect);
    
    return () => {
        socket.off('disconnect', onDisconnect);
    };
  }, []);

  if (!isReady) {
    return null; // or a loading indicator
  }

  return (
    <View style={[styles.container, { backgroundColor: colors.appBackground }]}>
      <StatusBar 
        backgroundColor={colors.appBackground}
        barStyle={themeType === 'dark' ? 'light-content' : 'dark-content'} 
      />
      <NavigationContainer 
        theme={navigationTheme}
        ref={navigationRef}
        initialState={{
          routes: [initialRoute]
        }}
      >
        <Stack.Navigator 
          initialRouteName={initialRoute.name} 
          screenOptions={{
            headerShown: true,
            headerStyle: {
              backgroundColor: colors.containerBackground,
              borderBottomColor: colors.inputBorder,
              borderBottomWidth: 1,
            },
            headerTitleStyle: {
              color: colors.titleText,
            },
            headerTintColor: colors.titleText,
            headerRight: () => <ThemeToggleButton />
          }}
        >
          <Stack.Screen 
            name="Login" 
            component={LoginScreen}
            options={{ 
              title: 'Sign In',
              headerLeft: () => null 
            }}
          />
          <Stack.Screen 
            name="Home" 
            component={HomeScreen} 
            options={{
              title: 'Home',
              headerLeft: () => null,
            }}
          />
          <Stack.Screen 
            name="Game" 
            component={GameScreen}
          />
          <Stack.Screen 
            name="GameCreation" 
            component={GameCreationScreen}
            options={{ title: 'Create Game' }}
          />
          <Stack.Screen 
            name="JoinGame" 
            component={JoinGameScreen}
            options={{ title: 'Join Game' }}
          />
        </Stack.Navigator>
      </NavigationContainer>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
});
