const ENV = {
    development: {
      socketUrl: 'http://localhost:3000',
      socketPath: '/socket.io/',
    },
    production: {
      socketUrl: 'https://suecatatau-server-production.up.railway.app',
      socketPath: '/socket.io/',
    }
  };
  
  // In development, the app can connect to your local server
  // In production, it will connect to the Railway server
  const getEnvironment = () => {
    if (__DEV__) {
      return ENV.development;
    }
    return ENV.production;
  };
  
  export default getEnvironment();